.navbar{position:fixed;top:0;left:0;width:100%;height:64px;padding:1rem 2rem;display:flex;justify-content:space-between;align-items:center;z-index:999;background-color:#000}.logo img{display:block;height:28px;width:auto}.burger{display:flex;flex-direction:column;justify-content:center;gap:5px;background:rgba(0,0,0,0);border:none;cursor:pointer;z-index:1000}.burger span{display:block;width:24px;height:2px;background:#fff}.menuOverlay{position:fixed;top:64px;left:0;right:0;bottom:0;z-index:998;display:flex;flex-direction:row;align-items:stretch;background:#000;color:#fff;opacity:0;pointer-events:none;transition:opacity .4s ease}.menuOverlay.open{opacity:1;pointer-events:auto}@media(max-width: 640px){.menuOverlay{flex-direction:column}}.menuContent{flex:1;display:flex;flex-direction:column;justify-content:space-between;padding:clamp(1.5rem,5vw,4rem);z-index:10;position:relative}@media(max-width: 640px){.menuContent{z-index:2}}.headline{font-family:var(--font-inter);font-size:3rem;font-weight:600;line-height:1.2;display:flex;flex-direction:column;align-items:flex-start;white-space:pre-wrap;gap:.8rem}.lineTop{min-height:3.6rem;font-size:2rem;font-weight:300;line-height:1.2;width:100%}.lineBottom{min-height:6.6rem;font-family:var(--font-cormorant);font-size:6rem;line-height:1.1;width:100%}@media(max-width: 640px){.lineBottom{font-size:4rem}}.menuText{font-family:var(--font-inter);font-size:1rem;line-height:1.6;max-width:600px;margin-top:2rem}.ctaButtons{display:flex;flex-wrap:wrap;gap:1rem;margin-top:2rem}.cta{font-family:var(--font-inter);padding:1rem 2rem;font-weight:600;text-transform:uppercase;border-radius:8px;text-decoration:none;transition:all .2s ease;display:inline-block;text-align:center}.cta.solid{background:#fff;color:#000;border:none}.cta.solid:hover{background:#eee;transform:translateY(-2px)}.cta.outlined{background:rgba(0,0,0,0);color:#fff;border:2px solid #fff}.cta.outlined:hover{background:hsla(0,0%,100%,.1);transform:translateY(-2px)}.posterWrapper{flex:1;display:flex;align-items:center;justify-content:flex-end;overflow:hidden}@media(max-width: 640px){.posterWrapper{position:absolute;inset:0;z-index:0}}.posterImage{height:100vh;width:auto;object-fit:cover;object-position:right center}@media(max-width: 640px){.posterImage{height:100%;width:100%;object-fit:cover;opacity:.8}}