.card{width:100%;max-width:100%;margin:2rem 0;display:flex;flex-direction:column;align-items:center;padding-inline:1rem}.videoWrapper{position:relative;width:100%;max-width:960px;aspect-ratio:16/9;overflow:hidden;border-radius:1.5rem}.video{width:100%;height:100%;object-fit:cover;display:block}.logo{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);max-width:400px;max-height:200px;opacity:.9;pointer-events:none}.textContent{margin-top:1.5rem;text-align:center;max-width:800px}.title{font-family:var(--font-inter);font-size:1.75rem;font-weight:600;color:#fff}.description{font-family:var(--font-inter);font-size:1rem;line-height:1.6;color:#ccc;margin-top:.5rem}